import { useNavigate } from 'react-router-dom';

import icon404 from '../../assets/icon404.svg';
import './ui.css';

export const NoFoundScreen = () => {

    const navigate = useNavigate();

    const handleReturn = () => {
        navigate('/');
    }

    return (
        <div className='no-found-container'>
            <img className='icon-404' src={icon404} alt='ícono de error 404' />
            <p className='title-404'>PÁGINA NO DISPONIBLE</p>
            <button
                className="btn-outline-info"
                onClick={ handleReturn }
            >
                REGRESAR A INICIO
            </button>
        </div>
    )
}
