import { types } from '../types/types';

const initialState = {
    activeInvoice: null,
    porcentajeIva: '',
    importaExistencia: '',
    tareaTerminada: '',
    idProductAux: '',
    savedCorrect: false,
    invoicesFind: null,
    invoiceFindSelected: false,
}

export const invoiceReducer = ( state = initialState, action ) => {

    switch (action.type) {

        case types.porcentajeIvaType:
            return {
                ...state,
                porcentajeIva: action.payload
            }

        case types.importaExistenciasType:
            return {
                ...state,
                importaExistencia: action.payload
            }

        case types.invoiceActiveType:
            return {
                ...state,
                activeInvoice: [action.payload]
            }
        
        case types.clearInvoiceActiveType:
            return {
                ...state,
                activeInvoice: null
            }

        case types.addNewNumberInvoiceActiveType:
            return {
                ...state,
                activeInvoice: [{
                    ...state.activeInvoice,
                    factura: action.payload
                }]
            }

        case types.beforeInvoiceActiveType:
            return {
                ...state,
                activeInvoice: action.payload
            }
        
        case types.putClientSearchingOnInvoiceType:
            const invoiceWithClientFind = { ...state.activeInvoice[0], ...action.payload };
            return {
                ...state,
                activeInvoice: [invoiceWithClientFind]
            }

        case types.addInfoCardValuesToActiveInvoiceType: 
            const invoiceWithCardValues = { ...state.activeInvoice[0], ...action.payload };
            return {
                ...state,
                activeInvoice: [invoiceWithCardValues]
            }
        
        case types.addSelectsCardValuesToActiveInvoiceType:
            const invoiceWithSelectsCardValues = { ...state.activeInvoice[0], ...action.payload };
            return {
                ...state,
                activeInvoice: [invoiceWithSelectsCardValues]
            }

        case types.addCommentCardValuesToActiveInvoiceType:
            const invoiceWithCommentCardValues = { ...state.activeInvoice[0], ...action.payload };
            return {
                ...state,
                activeInvoice: [invoiceWithCommentCardValues]
            }

        case types.saveInvoiceType:
            return {
                ...state,
                activeInvoice: [action.payload]
            }
        
        case types.savedCorrectActionType:
            return {
                ...state,
                savedCorrect: true
            }

        case types.clearSavedCorrectActionType:
            return {
                ...state,
                savedCorrect: false
            }

        case types.updateValuesAndListProductsOnInvoiceType:
            const invoiceWithProductsAndValuesUpdated = { ...state.activeInvoice[0], ...action.payload };
            return {
                ...state,
                activeInvoice: [invoiceWithProductsAndValuesUpdated]
            }

        case types.putProductNoValuesOnInvoiceEmptyType:
            const invoiceWithFirstProduct = { ...state.activeInvoice[0], listProducts: [{ ...action.payload }] };
            // action.payload.map( product => {
            //     invoiceWithProductsAndValuesUpdated1.productos.push(product);
            // }
            // action.payload: {listProducts: [{ ...action.payload }]}
            return {
                ...state,
                activeInvoice: [invoiceWithFirstProduct]
            }

        case types.putProductWithValuesOnInvoiceEmptyType:
            // const invoiceWithProductValues = { ...state.activeInvoice[0], listProducts: [...state.activeInvoice[0].listProducts, { ...action.payload }] };
            //Buscar el producto en la lista de productos
            const invoiceWithProductValues = { 
                ...state.activeInvoice[0], 
                listProducts: [...state.activeInvoice[0].listProducts.map( product => {
                    if(product.codigo.trim() === action.payload.codigo.trim()){
                        return { ...product, ...action.payload };
                    }
                    return product;
                })] 
            }
            // const invoiceWithProductValues = ;
            return {
                ...state,
                activeInvoice: [invoiceWithProductValues]
            }

        case types.putProductNoValuesOnInvoiceFillType:
            const invoiceWithProductValuesFill = {
                ...state.activeInvoice[0],
                listProducts: [ { ...action.payload }, ...state.activeInvoice[0].listProducts ]
            }
            return {
                ...state,
                activeInvoice: [invoiceWithProductValuesFill]
            }

        case types.clearProductAddNoValuesOnInvoiceType:
            if(state.activeInvoice[0].listProducts && state.activeInvoice[0].listProducts.length > 1 ){
                const invoiceWithProductValuesClear = {
                    ...state.activeInvoice[0],
                    listProducts: [...state.activeInvoice[0].listProducts.filter( product => product.codigo !== action.payload )]
                }
                return {
                    ...state,
                    activeInvoice: [invoiceWithProductValuesClear]
                }
            }
            if(state.activeInvoice[0].listProducts.length <= 1 )
                delete state.activeInvoice[0].listProducts
                
            return state;

        case types.deleteProductFromInvoiceType:
            const invoiceWithProductDeleted = {
                ...state.activeInvoice[0],
                listProducts: [...state.activeInvoice[0].listProducts.filter( product => product.codigo.trim() !== action.payload.trim() )]
            }
            return {
                ...state,
                activeInvoice: [invoiceWithProductDeleted]
            }
    
        case types.invoiceTaskFinishType: 
            return {
                ...state,
                tareaTerminada: action.payload
            }

        case types.idProductOnInvoiceActiveAuxType:
            return {
                ...state,
                idProductAux: action.payload
            }

        case types.clearIdProductOnInvoiceActiveAuxType:
            return {
                ...state,
                idProductAux: ''
            }

        case types.invoicesSearchingType:
            return {
                ...state,
                invoicesFind: action.payload
            }

        case types.invoiceFindSelectedActionType:
            return {
                ...state,
                invoiceFindSelected: true
            }

        default:
            return state;

    }

}