import Swal from 'sweetalert2';

import { fetchConToken } from '../helpers/fetch';
import { types } from '../types/types';
import { clearSearchingClients } from './clients';
import { uiCloseModal } from './ui';

export const startProductList = () => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( 'product/buscador/productos', { 
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt }, 
            'POST' );
        const body = await resp.json();
        
        if( body.ok ){
            ( body.msg.length > 0 ) && dispatch( productsList({ productList: body.msg }) );
        }else{
            Swal.fire({
                title: "Error",   
                text: body.msg,   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
        }

    }
}

const productsList = ( productList ) => ({
    type: types.productList,
    payload: productList
});

export const startProductSave = ( product, user ) => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( 'product/actions/productos', { 
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt,
            user,
            ...product }, 
            'POST' );
        const body = await resp.json();
        
        if( body.ok ){
            Swal.fire({
                title: "¡Guardado!",  
                icon: "success",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
            dispatch( productSave( body.msg ) );
            dispatch( clearSearchingClients() );
            dispatch( clearSearchingProducts() );
        }else{
            const { codigo, nombre, bodega, precio, impuesto, grupo, servicio } = body.errors || { 
                codigo: '', 
                nombre: '', 
                bodega: '', 
                precio: '', 
                impuesto: '', 
                grupo: '', 
                servicio: ''
            };

            if( codigo && codigo !== '' )
                Swal.fire({
                    title: "Error",   
                    text: codigo.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else if( nombre && nombre !== '' )
                Swal.fire({
                    title: "Error",   
                    text: nombre.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else if( bodega && bodega !== '' )
                Swal.fire({
                    title: "Error",   
                    text: bodega.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else if( precio && precio !== '' )
                Swal.fire({
                    title: "Error",   
                    text: precio.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else if( impuesto && impuesto !== '' )
                Swal.fire({
                    title: "Error",   
                    text: impuesto.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else if( grupo && grupo !== '' )
                Swal.fire({
                    title: "Error",   
                    text: grupo.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else if( servicio && servicio !== '' )
                Swal.fire({
                    title: "Error",   
                    text: servicio.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else
                Swal.fire({
                    title: "Error",   
                    text: body.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
        }

    }
}

const productSave = ( product ) => ({
    type: types.productSave,
    payload: product
});

