const startCheckProductInvoices = ( productCode ) => {
    return async() => {
        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( `product/actions/productos/${ productCode }`, { 
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt }, 
            'POST' );
        const body = await resp.json();
        
        if( body.ok )
            return true;
        else if( !body.ok )
            return false;
        else
            Swal.fire({
                title: "Error",   
                text: body.msg,   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });

    }
}

export const startDeleteProduct = ( productCodeDelete, bodega ) => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const productInvoices = await dispatch( startCheckProductInvoices( productCodeDelete.trim() ) );

        if( !productInvoices ){
            const resp = await fetchConToken( `product/actions/productos/${ productCodeDelete.trim() }`, { 
                userEncrypt, 
                passwordEncrypt, 
                databaseEncrypt,
                schemaEncrypt,
                bodega }, 
                'DELETE' );
            const body = await resp.json();
            
            if( body.ok ){
                Swal.fire({
                    title: "Eliminado",  
                    icon: "success",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
                dispatch( productDelete( body.deleted ) );
                dispatch( clearSearchingClients() );
                dispatch( clearSearchingProducts() );
                dispatch( productClearActive() );
            }else{
                Swal.fire({
                    title: "Error",   
                    text: body.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            }

        }else{
            Swal.fire({
                title: "Error",   
                text: 'El artículo tiene facturas asociadas',   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
        }

    }
}

const productDelete = ( product ) => ({
    type: types.productDelete,
    payload: product
});

export const startActiveProduct = ( productCode, productList, bodegaField ) => {
    return async( dispatch ) => {
        const product = productList.filter(c => (c.codigoa === productCode && c.bodega === bodegaField));
        dispatch( activeProduct( product[0] ) );
    }
} 

export const activeProduct = ( client ) => ({
    type: types.productActiveModal,
    payload: client
});

export const productClearActive = () => ({
    type: types.productClearActiveModal
});

export const startSearchingProducts = ( parameter ) => {
    return async( dispatch ) => {
        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( `product/buscador/productos/${ parameter }`, { 
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt }, 
            'POST' );
        const body = await resp.json();
        
        if( body.ok )
            dispatch( searchingProducts( body.msg ) );
        else
            Swal.fire({
                title: "Error",   
                text: body.msg,   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
    }
}

export const startSearchingProductsByBodega = ( parameter, bodega ) => {
    return async( dispatch ) => {
        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( `product/buscador/productos/${ parameter }`, { 
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt }, 
            'POST' );
        const body = await resp.json();
        
        if( body.ok ){
            const products = body.msg.filter( c => c.bodega === bodega );
            dispatch( searchingProducts( products ) );
        }
        else
            Swal.fire({
                title: "Error",   
                text: body.msg,   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
    }
}

