import Swal from 'sweetalert2';

import { fetchConToken } from '../helpers/fetch';
import { types } from '../types/types';
import { uiCloseModal } from './ui';

export const startClientList = () => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( 'client/buscador/clientes', { 
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt }, 
            'POST' );
        const body = await resp.json();
        
        if( body.ok ){
            ( body.msg.length > 0 ) && dispatch( clientList({ clientList: body.msg }) );
        }else{
            Swal.fire({
                title: "Error",   
                text: body.msg,   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
        }

    }
}

const clientList = ( clientList ) => ({
    type: types.clientList,
    payload: clientList
});

export const startClientSave = ( client, user ) => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( 'client/actions/clientes', { 
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt,
            user,
            ...client }, 
            'POST' );
        const body = await resp.json();
        
        if( body.ok ){
            Swal.fire({
                title: "¡Guardado!",
                icon: "success",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
            dispatch( clientSave( body.msg ) );
            dispatch( clearSearchingClients() );
        }else{
            const { codigo, ruc, nombre, correo } = body.errors || { 
                codigo: '', 
                ruc: '', 
                nombre: '', 
                correo: '' 
            };

            if( codigo && codigo !== '' )
                Swal.fire({
                    title: "Error",   
                    text: codigo.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else if( ruc && ruc !== '' )
                Swal.fire({
                    title: "Error",   
                    text: ruc.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else if( nombre && nombre !== '' )
                Swal.fire({
                    title: "Error",   
                    text: nombre.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else if( correo && correo !== '' )
                Swal.fire({
                    title: "Error",   
                    text: correo.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            else
                Swal.fire({
                    title: "Error",   
                    text: body.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
        }

    }
}

const clientSave = ( client ) => ({
    type: types.clientSave,
    payload: client
});

