const startCheckClientInvoices = ( clientCode ) => {
    return async() => {
        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( `client/actions/clientes/${ clientCode }`, { 
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt }, 
            'POST' );
        const body = await resp.json();
        
        if( body.ok )
            return true;
        else if( !body.ok )
            return false;
        else
            Swal.fire({
                title: "Error",   
                text: body.msg,   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });

    }
}

export const startDeleteClient = ( clientCodeDelete ) => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const clientInvoices = await dispatch( startCheckClientInvoices( clientCodeDelete ) );

        if( !clientInvoices ){
            const resp = await fetchConToken( `client/actions/clientes/${ clientCodeDelete }`, { 
                userEncrypt, 
                passwordEncrypt, 
                databaseEncrypt,
                schemaEncrypt }, 
                'DELETE' );
            const body = await resp.json();
            
            if( body.ok ){
                Swal.fire({
                    title: "Eliminado",   
                    text: body.msg,   
                    icon: "success",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
                dispatch( clientDelete( body.deleted ) );
                dispatch( clearSearchingClients() );
                dispatch( clientClearActiveClient() );
            }else{
                Swal.fire({
                    title: "Error",   
                    text: body.msg,   
                    icon: "error",   
                    showCancelButton: false,      
                    confirmButtonColor: "#0191CE",   
                    confirmButtonText: "OK",   
                    closeOnConfirm: false,   
                    closeOnCancel: false,
                    customClass: "Custom_Cancel"
                });
            }

        }else{
            Swal.fire({
                title: "Error",   
                text: 'El cliente tiene facturas asociadas',   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
        }

    }
}

const clientDelete = ( client ) => ({
    type: types.clientDelete,
    payload: client
});

export const actionModalClient = ( action ) => ({
    type: types.clientAction,
    payload: action
})

export const startActiveClient = ( clientCode, clientList ) => {
    return async( dispatch ) => {
        const client = clientList.find(c => c.codigoc === clientCode);
        dispatch( activeClient( client ) );
    }
} 

export const activeClient = ( client ) => ({
    type: types.clientActiveModal,
    payload: client
});

export const clientClearActiveClient = () => ({
    type: types.clientClearActiveModal
});

export const startSearchingClients = ( parameter ) => {
    return async( dispatch ) => {
        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( `client/buscador/clientes/${ parameter }`, { 
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt }, 
            'POST' );
        const body = await resp.json();
        
        if( body.ok )
            dispatch( searchingClients( body.msg ) );
        else
            Swal.fire({
                title: "Error",   
                text: body.msg,   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
    }
}

const searchingClients = ( clientsFind ) => ({
    type: types.clientSearching,
    payload: clientsFind
});

export const clearSearchingClients = () => ({
    type: types.clientClearSearching
});
