import { types } from '../types/types';

const initialState = {
    productList: [],
    groupProductList: [],
    productActive: null,
    productFind: null,
}

export const productReducer = ( state = initialState, action ) => {
    
    switch (action.type) {
        case types.productList:
            return {
                ...state,
                ...action.payload
            };
        
        case types.groupProducts:
            return {
                ...state,
                groupProductList: [...action.payload]
            };

        case types.productSave:
            return {
                ...state,
                productList: [
                    action.payload,
                    ...state.productList
                ]
            };
        
        case types.productEdit:
            return {
                ...state,
                productList: state.productList.map(
                    c => ( c.codigoa === action.payload.codigoa && c.bodega === action.payload.bodega ) ? action.payload : c
                )
            };
        
        case types.productDelete:
            console.log(action.payload);
            return {
                ...state,
                productList: state.productList.filter(
                    c => c.codigoa !== action.payload.codigoa
                )
            };

        case types.productActiveModal:
            return {
                ...state,
                productActive: action.payload
            };

        case types.productClearActiveModal:
            return {
                ...state,
                productActive: null
            }

        case types.productSearching:
            return {
                ...state,
                productFind: action.payload
            };

        case types.productClearSearching:
            return {
                ...state,
                productFind: null
            }

        default:
            return state;
    }

}