import { Navigate } from 'react-router-dom';

import { checkCredentials } from '../helpers/checkCredentials';

export const PrivateRoute = ({ children }) => {

    const user = localStorage.getItem( 'p1');
    const password = localStorage.getItem( 'p2');
    const database = localStorage.getItem( 'p3');

    //Comprobar que las credenciales sean correctas
    let resp = false; 
    if( user )
        resp = checkCredentials(user, password, database);

    return !!resp
        ? children
        : <Navigate to="/credenciales" />
}
