import { lazy, Suspense } from 'react';
import { Route, Routes } from 'react-router';

import { SideBar } from '../components/ui/SideBar';
import { Navbar } from '../components/ui/Navbar';
import { NoFoundScreen } from '../components/ui/NoFoundScreen';
import './router.css';
const HomeScreen = lazy( () => import('../components/home/HomeScreen') );
const ClientesScreen = lazy( () => import('../components/clientes/ClientesScreen') );
const ArticulosScreen = lazy( () => import('../components/articulos/ArticulosScreen') );
const FacturasScreen = lazy( () => import('../components/facturas/FacturasScreen') );

export const DashboardRoutes = () => {

    return (
        <>
            <div className="container">
                <SideBar />
                
                <Navbar />

                <Routes>
                    <Route path="/" element={
                        <Suspense fallback={
                            <div className='container-loading'>
                                <i className="fa fa-circle-o-notch fa-spin fa-3x loading"></i>
                            </div> 
                        }>
                            <HomeScreen />
                        </Suspense>
                    } />

                    <Route path="clientes" element={
                        <Suspense fallback={
                            <div className='container-loading'>
                                <i className="fa fa-circle-o-notch fa-spin fa-3x loading"></i>
                            </div> 
                        }>
                            <ClientesScreen />
                        </Suspense>
                    } />

                    <Route path="articulos" element={
                        <Suspense fallback={
                            <div className='container-loading'>
                                <i className="fa fa-circle-o-notch fa-spin fa-3x loading"></i>
                            </div> 
                        }>
                            <ArticulosScreen />
                        </Suspense>
                    } />

                    <Route path="facturas" element={
                        <Suspense fallback={
                            <div className='container-loading'>
                                <i className="fa fa-circle-o-notch fa-spin fa-3x loading"></i>
                            </div> 
                        }>
                            <FacturasScreen />
                        </Suspense>
                    } />
    
                    <Route path="/*" element={<NoFoundScreen />} />
                </Routes>
            </div>
        </>
    )
}
