import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { clearSearchingClients, startSearchingClients } from '../../actions/clients';
import { clearSearchingInvoices } from '../../actions/invoice';
import { clearSearchingProducts, startSearchingProducts } from '../../actions/products';
import { uiForceHiddenSidebar, uiOpenSearchInvoiceModal, uiSendParamSearchInvoiceModal, uiToggleSidebar } from '../../actions/ui';
import logoScaeFill from '../../assets/logoScaeFill.svg';
import menuVertical from '../../assets/menuVertical.svg';
import profilePicture from '../../assets/profilePicture.svg';
import './ui.css';

const initValue = { searchInput: '' }

export const Navbar = () => {

    const dispatch = useDispatch();
    const { user } = useSelector( state => state.auth );
    const { view, actionView } = useSelector( state => state.ui );
    
    const [width, setWidth] = useState(window.innerWidth);
    const [ thisPage, setThisPage ] = useState('');
    const [ thisActionInvoice, setThisActionInvoice ] = useState('');

    const [searchValues, setSearchValues] = useState( initValue );

    const { searchInput } = searchValues;

    useEffect(() => {
        setThisPage( view );
        setThisActionInvoice( actionView );
        setSearchValues( initValue );
        dispatch( clearSearchingClients() );
        dispatch( clearSearchingProducts() );
        dispatch( clearSearchingInvoices() );
    }, [dispatch, view, actionView]);

    useEffect(() => {
        const handleResize = () => {
            setWidth(window.innerWidth);
        }
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, [width]);
    
    useEffect(() => {
        if(width <= 960)
            dispatch( uiForceHiddenSidebar() );
    }, [ width, dispatch ]);
    
    const handleInputChange = ({ target }) => {
        setSearchValues({
            ...searchValues,
            [target.name]: target.value
        });
    }

    const handleSidebar = () => {
        //Cambiar el estado sidebar en store a true y colocar la clase active en SidebarScreen
        dispatch( uiToggleSidebar() );
    }

    const handleSearch = (e) => {
        e.preventDefault();
        
        ( thisPage === 'facturas' ) && dispatch( uiOpenSearchInvoiceModal() );
        ( thisPage === 'facturas' ) && dispatch( uiSendParamSearchInvoiceModal(searchInput) );
        if( searchInput !== '' ){
            ( thisPage === 'clientes' ) && dispatch( startSearchingClients( searchInput ) );
            ( thisPage === 'articulos' ) && dispatch( startSearchingProducts( searchInput ) );
        }else{
            dispatch( clearSearchingClients() );
            dispatch( clearSearchingProducts() );
        }
        
    }

    return (
        <div className='navbar'>
            <div className='navbar-container'>

                <div className='navbar-mobile'>
                    <div 
                        className='navbar-menu'
                        onClick={ handleSidebar }
                    >
                        <img src={menuVertical} alt='menú desplegable' />
                    </div>

                    <div className='navbar-logo'>
                        <img src={logoScaeFill} alt='logo' />
                        <h1>SCAE</h1>
                    </div>
                </div>

                <form>
                    <div className='search-form'>
                        <input
                            // disabled={ (thisPage !== 'inicio') ? false : true }
                            disabled={ ( thisPage === 'inicio' || thisActionInvoice === 'newInvoice' || thisActionInvoice === 'editInvoice' ) 
                                            ? true : false 
                                    }
                            type='text' 
                            autoComplete='off'
                            placeholder='Buscar ...'
                            name='searchInput'
                            value={ searchInput }
                            onChange={ handleInputChange }
                        />
                        <button
                            disabled={ (thisPage !== 'inicio') ? false : true } 
                            onClick={ handleSearch }
                        >
                            <i className='fa fa-search'></i>
                        </button>
                    </div>
                </form>

                <div className='navbar-user'>
                    <img src={profilePicture} alt='Foto de perfil' />
                    <p>{user}</p>
                </div>
            </div>
        </div>
    )
}
