import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    BrowserRouter,
    Routes,
    Route
} from 'react-router-dom';
import { startCheckingCredentials } from '../actions/auth';

import { CredentialsScreen } from '../components/auth/CredentialsScreen';
import { PrivateRoute } from './PrivateRoute';
import { PublicRoute } from './PublicRoute';
import { ScaeRoute } from './ScaeRoute';
import './router.css';

export const AppRouter = () => {

    const dispatch = useDispatch();
    const { checking, user } = useSelector(state => state.auth);

    useEffect(() => {
        if( user )
            dispatch( startCheckingCredentials() );
    }, [dispatch, user]);

    if( checking && user ){
        return (
            <div className='container-loading'>
                <i className="fa fa-circle-o-notch fa-spin fa-3x loading"></i>
            </div>
        );
    }

    return (
        <BrowserRouter>
            <Routes>

                <Route path="/credenciales" element={
                        <PublicRoute>
                            <CredentialsScreen />
                        </PublicRoute>
                    } 
                />

                <Route path="/*" element={
                        <PrivateRoute>
                            <ScaeRoute />
                        </PrivateRoute>
                    } 
                />

            </Routes>
        </BrowserRouter>
    )
}