import { useDispatch, useSelector } from 'react-redux';
import { NavLink } from 'react-router-dom';

import './ui.css';
import logoScaeFill from '../../assets/logoScaeFill.svg';
import logoutIcon from '../../assets/logoutIcon.svg';
import { uiForceHiddenSidebar } from '../../actions/ui';

export const SideBar = () => {

    const dispatch = useDispatch();
    
    const { sidebar }  = useSelector( state => state.ui );

    const handleHiddenSidebar = () => {
        dispatch( uiForceHiddenSidebar() );
    }

    const handleLogout = () => {
        localStorage.removeItem('token');
        localStorage.removeItem('p4');
        localStorage.removeItem('active');

        window.location.href = '/login';
    }

    return (
        <div className={sidebar ? 'sidebar active-sidebar' : 'sidebar '}>
            <div className='sidebar-logo'>
                <img src={logoScaeFill} alt='logo' />
                <h1>SCAE</h1>
            </div>
            
            <h2>Menú</h2>

            <ul>
                <li>
                    <NavLink
                        className={ ({ isActive }) => 'nav-link ' + (isActive ? 'active' : '')}
                        onClick={ handleHiddenSidebar }
                        to="/"
                    >
                        <div className='icon-li-home' alt='ícono de inicio'></div> 
                        Inicio
                        <div className='circle-active' alt='ícono de inicio'></div>
                    </NavLink>
                </li>

                <li>
                    <NavLink
                        className={ ({ isActive }) => 'nav-link ' + (isActive ? 'active' : '')}
                        onClick={ handleHiddenSidebar }
                        to="/clientes"
                    >
                        <div className='icon-li-client' alt='ícono de cliente'></div>
                        Clientes
                        <div className='circle-active' alt='ícono de cliente'></div>
                    </NavLink>
                </li>

                <li>
                    <NavLink
                        className={ ({ isActive }) => 'nav-link ' + (isActive ? 'active' : '')}
                        onClick={ handleHiddenSidebar }
                        to="/articulos"
                    >
                        <div className='icon-li-article' alt='ícono de artículo'></div>
                        Artículos
                        <div className='circle-active' alt='ícono de artículo'></div>
                    </NavLink>
                </li>

                <li>
                    <NavLink
                        className={ ({ isActive }) => 'nav-link ' + (isActive ? 'active' : '')}
                        onClick={ handleHiddenSidebar }
                        to="/facturas"
                    >
                        <div className='icon-li-invoice' alt='ícono de factura'></div>
                        Facturas
                        <div className='circle-active' alt='ícono de factura'></div>
                    </NavLink>
                </li>
            </ul>
                
            <button 
                className='logout' 
                onClick={ handleLogout }
            >
                <img src={logoutIcon} className='icon-li' alt='ícono de inicio'></img> 
                Cerrar sesión
            </button>
        </div>
    )
}
