import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    Routes,
    Route
} from 'react-router-dom';

import { startChecking } from '../actions/auth';
import { LoginScreen } from '../components/auth/LoginScreen';
import { EnterprisesRoute } from './EnterprisesRoute';
import { PrivateScaeRoute } from './PrivateScaeRoute';
import { PublicScaeRoute } from './PublicScaeRoute';
import './router.css';

export const ScaeRoute = () => {

    const dispatch = useDispatch();
    const { checking } = useSelector(state => state.auth);

    useEffect(() => {
        dispatch( startChecking() );
    }, [dispatch]);

    if( checking ){
        return (
            <div className='container-loading'>
                <i className="fa fa-circle-o-notch fa-spin fa-3x loading"></i>
            </div>
        );
    }

    return (
        <Routes>

            <Route path="/login" element={
                    <PublicScaeRoute>
                        <LoginScreen />
                    </PublicScaeRoute>
                } 
            />

            <Route path="/*" element={
                    <PrivateScaeRoute>
                        <EnterprisesRoute />
                    </PrivateScaeRoute>
                } 
            />
                
        </Routes>
    )

}