import { types } from "../types/types";

const initialState = {
    checking: true,
}

export const authReducer = ( state = initialState, action ) => {
    
    switch (action.type) {

        case types.authLogin:
            return {
                ...state,
                ...action.payload,
                active: localStorage.getItem('active') || null,
                checking: false,
            };
            
            
        case types.authEmpresas:
            return {
                    ...state,
                    active: localStorage.getItem('active') || null,
                    ...action.payload,
                    checking: false,
            };
            
        case types.enterpriseActive:
            return {
                ...state,
                ...action.payload,
                checking: false,
            };

        case types.authCheckingFinish:
            return {
                ...state,
                active: localStorage.getItem('active') || null,
                checking: false
            };
    
        default:
            return state;
    }

}