import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import Swal from 'sweetalert2';

import './auth.css';
import logoScae from '../../assets/logoScae.svg';
import iconCredentials from '../../assets/iconCredentials.svg';
import { useForm } from '../../hooks/useForm';
import { startCredentials } from '../../actions/auth';

export const CredentialsScreen = () => {
    
    const dispatch = useDispatch();

    const [isActiveInputUser, setIsActiveInputUser] = useState(false);
    const [isActiveInputPass, setIsActiveInputPass] = useState(false);
    const [isActiveInputDB, setIsActiveInputDB] = useState(false);
    const [isLoadingCredentials, setIsLoadingCredentials] = useState(false);
    const [isDisableCredentials, setIsDisableCredentials] = useState(false);

    useEffect(() => {
        return () => {
            enableButton()
        }
    }, [])
    

    const { values, handleInputChange } = useForm({
        userValue:'',
        passValue:'',
        dbValue:''
    });
    
    const { userValue, passValue, dbValue } = values;
    
    const activeInputUser = () => {
        document.querySelector(`input[name='userValue']`).focus();
        setIsActiveInputUser(true);
        setIsActiveInputPass(false);
        setIsActiveInputDB(false);
    }

    const activeInputPass = () => {
        document.querySelector(`input[name='passValue']`).focus();
        setIsActiveInputUser(false);
        setIsActiveInputPass(true);
        setIsActiveInputDB(false);
    }

    const activeInputDB = () => {
        document.querySelector(`input[name='dbValue']`).focus();
        setIsActiveInputUser(false);
        setIsActiveInputPass(false);
        setIsActiveInputDB(true);
    }

    const disableButton = () => {
        setIsLoadingCredentials(true);
        setIsDisableCredentials(true);
    }

    const enableButton = () => {
        setIsLoadingCredentials(false);
        setIsDisableCredentials(false);
    }

    const handleCredentials = (e) => {
        e.preventDefault();

        disableButton();
        
        if( userValue !== '' && passValue !== '' && dbValue !== '' ){
            dispatch( startCredentials( userValue, passValue, dbValue ) );
            setTimeout(() => {
                enableButton();
            }, 2000);
        } else{
            Swal.fire({
                title: "Error",   
                text: 'Asegúrese de no dejar campos vacíos',   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
            setTimeout(() => {
                enableButton();
            }, 500);
        }
        
    }

    return (
        <div className='main-container'>
            
            <div className='card-form'>
                <img className='icon-credentials' src={iconCredentials} alt='Logo credenciales' />
                <section className='form-presentation'>
                    <section className='information'>
                        <img className='form-logo' src={logoScae} alt='Logo SCAE' />
                        <p className='form-title-presentation'>CREDENCIALES</p>
                        <p className='form-title-presentation'>SCAE</p>
                    </section>
                </section>

                <div className='form-container'>
                    <p className='title-form'>Ingrese sus credenciales</p>
                    <form onSubmit={ handleCredentials }>
                        <div>
                            <p className='title-input'>Usuario</p>
                            <div className={`input-icono-user ${isActiveInputUser ? 'active-user' : ''}`} onClick={ activeInputUser }>
                                <input 
                                    autoComplete="off" 
                                    name='userValue' 
                                    value={userValue} 
                                    type="text" 
                                    placeholder='miusuario' 
                                    onFocus={ () => setIsActiveInputUser(true) } 
                                    onChange={ handleInputChange }
                                />
                            </div>
                        </div>
                        <div>
                            <p className='title-input'>Contraseña</p>
                            <div className={`input-icono-pass ${isActiveInputPass ? 'active-pass' : ''}`} onClick={ activeInputPass }>
                                <input 
                                    name='passValue' 
                                    value={passValue} 
                                    type="password" 
                                    onFocus={ () => setIsActiveInputPass(true) }
                                    onChange={ handleInputChange } 
                                />
                            </div>
                        </div>
                        <div>
                            <p className='title-input'>Base de datos</p>
                            <div className={`input-icono-db ${isActiveInputDB ? 'active-db' : ''}`} onClick={ activeInputDB }>
                                <input 
                                    autoComplete="off" 
                                    name='dbValue'
                                    value={dbValue}  
                                    type="text" 
                                    placeholder='mibasededatos' 
                                    onFocus={ () => setIsActiveInputDB(true) } 
                                    onChange={ handleInputChange }
                                />
                            </div>
                        </div>
                        <div>
                            <button
                                disabled={isDisableCredentials}
                                className={isDisableCredentials ? 'button-form-block' : 'button-form' }
                            >
                                {isLoadingCredentials 
                                    ? <p><i className="fa fa-circle-o-notch fa-spin"></i> Verificando</p>
                                    : 'Enviar credenciales'
                                }
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}