import Swal from 'sweetalert2';

import { fetchConToken, fetchSinToken } from '../helpers/fetch';
import { types } from '../types/types';

export const startCredentials = ( user, password, database ) => {
    return async( dispatch ) => {

        const resp = await fetchSinToken( 'auth', { user, password, database }, 'POST' );
        const body = await resp.json();

        if( body.ok ){

            localStorage.setItem( 'p1', body.user );
            localStorage.setItem( 'p2', body.password );
            localStorage.setItem( 'p3', body.database );

            dispatch( checkingFinish() );

        }else{
            Swal.fire({
                title: 'Error',
                text: body.msg,
                icon: 'error',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }

    }
}

export const startCheckingCredentials = () =>( {
    type: types.authCheckingFinish
});

export const startLogin = ( userScae, passwordScae ) => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');

        const resp = await fetchSinToken( 'auth/login', { 
                userEncrypt, 
                passwordEncrypt, 
                databaseEncrypt, 
                userScae, 
                passwordScae }, 
            'POST' );
        const body = await resp.json();

        if( body.ok ){
            localStorage.setItem( 'token', body.token );
            localStorage.setItem( 'token-init-date', new Date().getTime() );

            dispatch( login({
                user: userScae.toUpperCase().trim(),
            }) );
        }else{
            Swal.fire({
                title: 'Error',
                text: body.msg,
                icon: 'error',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }

    }
}

export const startChecking = () => {
    return async( dispatch ) => {

        const resp = await fetchConToken( 'auth/renew' );
        const body = await resp.json();

        if( body.ok ){
            localStorage.setItem( 'token', body.token );
            localStorage.setItem( 'token-init-date', new Date().getTime() );
    
            dispatch( login({
                user: body.user
            }) );
        }else{
            dispatch( checkingFinish() );
        }

    }
}

const login = ( user ) => ({
    type: types.authLogin,
    payload: user
});

export const startEnterpriseSelect = () => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');

        const resp = await fetchConToken( 'auth/empresas', { 
                userEncrypt, 
                passwordEncrypt, 
                databaseEncrypt }, 
                'POST' );
        const body = await resp.json();

        if( body.ok ){
            dispatch( enterpriseSelect({
                enterprises: body.msg
            } ) );
        }else{
            Swal.fire({
                title: 'Error',
                text: body.msg,
                icon: 'error',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }

    }
}

const enterpriseSelect = ( empresas ) => ({
    type: types.authEmpresas,
    payload: empresas
});

export const startActiveEmpresa = ( code ) => {
    return async( dispatch ) => {
        
        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');

        const resp = await fetchConToken( 'auth/miempresa', { 
                userEncrypt, 
                passwordEncrypt, 
                databaseEncrypt,
                code }, 
                'POST' );
        const body = await resp.json();

        if( body.ok ){

            localStorage.setItem( 'p4', body.schema );
            localStorage.setItem('active', body.msg.codigo.trim());
            
            dispatch( activeEmpresa( {
                active: body.msg.codigo.trim()
            } ) );
            
        }else{
            Swal.fire({
                title: 'Error',
                text: body.msg,
                icon: 'error',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }

    }
}

const activeEmpresa = ( enterprise ) => ({
    type: types.enterpriseActive,
    payload: enterprise
});

export const checkingFinish = () => ({
    type: types.authCheckingFinish
});

