import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import Swal from 'sweetalert2';

import { useForm } from '../../hooks/useForm';
import './auth.css';
import logoScae from '../../assets/logoScae.svg';
import iconLogin from '../../assets/iconLogin.svg';
import { startLogin } from '../../actions/auth';

export const LoginScreen = () => {

    const dispatch = useDispatch();
    
    const [isActiveInputUser, setIsActiveInputUser] = useState(false);
    const [isActiveInputPass, setIsActiveInputPass] = useState(false);
    const [isLoadingLogin, setIsLoadingLogin] = useState(false);
    const [isDisableLogin, setIsDisableLogin] = useState(false);

    useEffect(() => {
        return () => {
            enableButton()
        }
    }, [])

    const { values, handleInputChange } = useForm({
        userValue:'',
        passValue:''
    });

    const { userValue, passValue } = values;

    const activeInputUser = () => {
        document.querySelector(`input[name='userValue']`).focus();
        setIsActiveInputUser(true);
        setIsActiveInputPass(false);
    }

    const activeInputPass = () => {
        document.querySelector(`input[name='passValue']`).focus();
        setIsActiveInputUser(false);
        setIsActiveInputPass(true);
    }

    const disableButton = () => {
        setIsLoadingLogin(true);
        setIsDisableLogin(true);
    }

    const enableButton = () => {
        setIsLoadingLogin(false);
        setIsDisableLogin(false);
    }

    const handleLogin = (e) => {
        e.preventDefault();

        disableButton();

        if( userValue !== '' && passValue !== '' ){
            dispatch( startLogin( userValue, passValue ) );

            setTimeout(() => {
                enableButton();
            }, 2000);
        }else{
            Swal.fire({
                title: "Error",   
                text: 'Asegúrese de no dejar campos vacíos',   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
            setTimeout(() => {
                enableButton();
            }, 500);
        }
    }

    return (
        <div className='main-container'>
            <div className='card-form'>
                <img className='icon-login' src={iconLogin} alt='Logo credenciales' />
                <section className='form-presentation'>
                    <section className='information info-login'>
                        <img className='form-logo' src={logoScae} alt='Logo SCAE' />
                        <p className='form-title-presentation'>LOGIN</p>
                        <p className='form-title-presentation'>SCAE</p>
                    </section>
                </section>

                <div className='form-container'>
                    <p className='title-form-login'>Ingrese sus datos</p>
                    <form onSubmit={ handleLogin }>
                        <div>
                            <p className='title-input'>Usuario</p>
                            <div className={`input-icono-user ${isActiveInputUser ? 'active-user' : ''}`} onClick={ activeInputUser }>
                                <input 
                                    autoComplete="off" 
                                    name='userValue' 
                                    value={userValue} 
                                    type="text" 
                                    placeholder='MIUSUARIO' 
                                    onFocus={ () => setIsActiveInputUser(true) } 
                                    onChange={ handleInputChange } 
                                />
                            </div>
                        </div>
                        <div>
                            <p className='title-input'>Contraseña</p>
                            <div className={`input-icono-pass ${isActiveInputPass ? 'active-pass' : ''}`} onClick={ activeInputPass }>
                                <input 
                                    name='passValue' 
                                    value={passValue}  
                                    type="password" 
                                    onFocus={ () => setIsActiveInputPass(true) } 
                                    onChange={ handleInputChange } 
                                />
                            </div>
                        </div>
                        
                        <div className='container-button'>
                            <button
                                disabled={isDisableLogin}
                                className={isDisableLogin ? 'button-form-block' : 'button-form' }
                            >
                                {isLoadingLogin 
                                    ? <p><i className="fa fa-circle-o-notch fa-spin"></i> Verificando</p>
                                    : 'Iniciar sesión'
                                }
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}