import { types } from '../types/types';

const initialState = {
    sellerList: [],
}

export const sellerReducer = ( state = initialState, action ) => {

    switch (action.type) {

        case types.sellerListType:
            return {
                ...state,
                sellerList: [...action.payload]
            };

        default:
            return state;
    }

}