import { types } from "../types/types";

const initialState = {
    storeList: [],
    storeAux: null,
}

export const bodegaReducer = ( state = initialState, action ) => {
    
    switch (action.type) {

        case types.bodegaList:
            return {
                ...state,
                storeList: [...action.payload]
            };

        case types.bodegaAux:
            return {
                ...state,
                storeAux: action.payload
            };
    
        default:
            return state;
    }

}