export const startPorcentajeIva = () => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( 'invoice/porcentajeiva', { 
                userEncrypt, 
                passwordEncrypt, 
                databaseEncrypt,
                schemaEncrypt }, 
                'POST' );
        const body = await resp.json();
        
        if( body.ok ){
            dispatch( porcentajeIVA( body.msg ));
        }else{
            Swal.fire({
                title: 'Error',
                text: body.msg,
                icon: 'error',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }
    }
}

const porcentajeIVA = ( porcentaje ) => ({
    type: types.porcentajeIvaType,
    payload: porcentaje
});

