export const startInvoiceActive = () => {
    return async( dispatch ) => {
        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( 'invoice/ultimafactura', { 
                userEncrypt, 
                passwordEncrypt, 
                databaseEncrypt,
                schemaEncrypt }, 
                'POST' );
        const body = await resp.json();
        
        if( body.ok ){
            if( Object.keys(body.msg).length > 0 ){
                await dispatch( invoiceActive( body.msg ) );
                
                await dispatch( startPorcentajeIva() );
                await dispatch( startImportaExistencias() );
            }else{
                await dispatch( invoiceActive({}) );

                await dispatch( startPorcentajeIva() );
                await dispatch( startImportaExistencias() );
            }

        }else{
            Swal.fire({
                title: 'Error',
                text: body.msg,
                icon: 'error',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }

    }
}

