export const startEditInvoice = ( invoice, user, numInvoice ) => {
    return async( dispatch ) => {
        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');
        const schemaEncrypt = localStorage.getItem( 'p4');

        const resp = await fetchConToken( `invoice/actions/factura/${ numInvoice }`, {
            userEncrypt, 
            passwordEncrypt, 
            databaseEncrypt,
            schemaEncrypt,
            user,
            ...invoice }, 
            'PUT' );
        const body = await resp.json();
        
        if( body.ok ){
            //Eliminar la actionView para desbloquear el buscar en el navbar
            dispatch( onViewAction( '' ) );
            //Limpiar activeInvoice, y demás campos
            dispatch( invoiceTaskFinish('') );

            Swal.fire({
                title: '¡Guardado!',
                icon: 'success',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
            

            //Agregar al activeInvoice la factura guardada
            await dispatch( invoiceActive({}) );
            await dispatch( invoiceActive( body.msg ) );
            
            //Indicador de Bloquear / desbloquear inputs al cargar
            dispatch( startSavedCorrectAction() );

        }else{
            Swal.fire({
                title: 'Error',
                text: body.msg,
                icon: 'error',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }
    }
}

