import { types } from '../types/types';

const initialState = {
    cardsEnterprises: false,
    sidebar: false,
    modalOpen: false,
    view: null,
    actionView: null,
    modalOpenArticles: false,
    modalOpenEditArticles: false,
    newArticleModal: false,
    openNewArticleWasOpened: false,
    modalSearchInvoice: false,
    paramSearchValueUi: null,
}

export const uiReducer = ( state = initialState, action ) => {
    
    switch (action.type) {
        case types.uiHiddenCardsEnterprises: 
            return {
                ...state,
                cardsEnterprises: true
            };

        case types.uiToggleSidebar:
            return {
                ...state,
                sidebar: !state.sidebar
            };

        case types.uiForceHiddenSidebar:
            if( state.sidebar === true ){
                return {
                    ...state,
                    sidebar: false
                };
            }else{
                return state;
            }

        case types.uiOpenModal:
            return {
                ...state,
                modalOpen: true
            };
    
        case types.uiCloseModal:
            return {
                ...state,
                modalOpen: false
            };

        case types.onView:
            return {
                ...state,
                view: action.payload
            };

        case types.onViewAction:
            return {
                ...state,
                actionView: action.payload
            };

        case types.uiOpenModalArticles:
            return {
                ...state,
                modalOpenArticles: true
            };

        case types.uiCloseModalArticles:
            return {
                ...state,
                modalOpenArticles: false
            };

        case types.uiOpenModalEditArticles:
            return {
                ...state,
                modalOpenEditArticles: true
            };

        case types.uiCloseModalEditArticles:
            return {
                ...state,
                modalOpenEditArticles: false
            };

        case types.uiActionModalNewArticle:
            return {
                ...state,
                newArticleModal: true
            };

        case types.uiClearActionModalNewArticle:
            return {
                ...state,
                newArticleModal: false
            };

        case types.modalNewArticleWasOpened:
            return {
                ...state,
                openNewArticleWasOpened: true
            };

        case types.clearModalNewArticleWasOpened:
            return {
                ...state,
                openNewArticleWasOpened: false
            };
        
        case types.uiOpenSearchInvoiceModal:
            return {
                ...state,
                modalSearchInvoice: true
            };

        case types.uiCloseSearchInvoiceModal:
            return {
                ...state,
                modalSearchInvoice: false
            };

        case types.uiSendParamSearchInvoiceModal:
            return {
                ...state,
                paramSearchValueUi: action.payload
            };

        default:
            return state;
    }

}