import { types } from '../types/types';

const initialState = {
    clientList: [],
    actionModal: null,
    clientActive: null,
    clientsFind: null,
}

export const clientReducer = ( state = initialState, action ) => {
    
    switch (action.type) {
        case types.clientList:
            return {
                ...state,
                ...action.payload
            };

        case types.clientSave:
            return {
                ...state,
                clientList: [
                    action.payload,
                    ...state.clientList
                ]
            };
        
        case types.clientEdit:
            return {
                ...state,
                clientList: state.clientList.map(
                    c => ( c.codigoc === action.payload.codigoc ) ? action.payload : c
                )
            };
        
        case types.clientDelete:
            return {
                ...state,
                clientList: state.clientList.filter(
                    c => c.codigoc !== action.payload.codigoc
                )
            };
        
        case types.clientAction:
            return {
                ...state,
                actionModal: action.payload
            };

        case types.clientActiveModal:
            return {
                ...state,
                clientActive: action.payload
            };

        case types.clientClearActiveModal:
            return {
                ...state,
                clientActive: null
            }

        case types.clientSearching:
            return {
                ...state,
                clientsFind: action.payload
            };

        case types.clientClearSearching:
            return {
                ...state,
                clientsFind: null
            }
            
        default:
            return state;
    }

}