import { lazy, Suspense } from 'react';
import { Route, Routes } from 'react-router-dom';
import { useSelector } from 'react-redux';

import { DashboardRoutes } from './DashboardRoutes';
import { HomeRoute } from './HomeRoute';
import { SelectEnterprisesRoute } from './SelectEnterprisesRoute';
import './router.css';
const EmpresasScreen = lazy( () => import('../components/empresas/EmpresasScreen') );

export const EnterprisesRoute = () => {

    const { checking, active } = useSelector(state => state.auth);

    if( checking && active ){
        return (
            <div className='container-loading'>
                <i className="fa fa-circle-o-notch fa-spin fa-3x loading"></i>
            </div>
        );
    }

    return (
        <Routes>
            <Route path="/*" element={
                    <HomeRoute>
                        <DashboardRoutes />
                    </HomeRoute>
                } 
            />
            
            <Route path="/empresas" element={
                    <SelectEnterprisesRoute>
                        <Suspense fallback={
                            <div className='container-loading'>
                                <i className="fa fa-circle-o-notch fa-spin fa-3x loading"></i>
                            </div> 
                        }>
                            <EmpresasScreen />
                        </Suspense>
                    </SelectEnterprisesRoute>
                } 
            />

        </Routes>
    )
}
